.main {
    max-width: 1200px;
    margin: 0 auto;
    padding: 5px 6px;
    box-sizing: border-box;
    @media (max-width: 1200px) {
        max-width: 800px;
    }
    @media (max-width: 800px) {
        max-width: 600px;
    }
    @media (max-width: 600px) {
        max-width: 400px;
    }
}